import {
  Container,
  Grid,
  Typography,
  Box,
  CssBaseline,
  Button,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import AccountCard from '../components/AccountCard';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

const theme1 = createTheme({
  palette: {
    primary: {
      main: '#BE3144', // Example primary color, adjust as needed
    },
    secondary: {
      main: '#1E1E1E', // Dark background color
    },
    background: {
      default: '#1E1E1E',
      paper: '#333333', // Darker paper color for cards
    },
    text: {
      primary: '#FFFFFF', // White text color
      secondary: '#BDBDBD', // Grayish secondary text color
    },
  },
  typography: {
    fontFamily: 'Roboto, sans-serif',
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          borderRadius: '12px',
          padding: '12px 24px',
          fontWeight: 'bold',
        },
      },
    },
    MuiCard: {
      styleOverrides: {
        root: {
          backgroundColor: '#333333',
          color: '#FFFFFF',
        },
      },
    },
  },
});

function Account() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const cardSpacing = isMobile ? 2 : 3;

  return (
    <ThemeProvider theme={theme1}>
      <CssBaseline />
      <Container maxWidth="lg">
        <Box sx={{ my: 4, textAlign: 'center', color: 'white' }}>
          <Typography variant="h4" component="h1" gutterBottom>
            ki.fg.wam
          </Typography>
          <Typography variant="h6" gutterBottom>
            Dashboard
          </Typography>

          <Grid container spacing={cardSpacing} sx={{ mt: 2 }}>
            <Grid item xs={12} sm={6}>
              <AccountCard title="Total WAX Balance:" value="6.12354672" />
            </Grid>
            <Grid item xs={12} sm={6}>
              <AccountCard title="Total USD Balance:" value="$1.9321245" />
            </Grid>
          </Grid>

          <Typography variant="body2" sx={{ mt: 2 }}>
            View on wax.bloks.io
          </Typography>

          <Grid container spacing={cardSpacing} sx={{ mt: 2 }}>
            <Grid item xs={12} sm={6} md={4}>
              <AccountCard title="Requested CPU for self total:" value="6.12354" />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <AccountCard title="Update Collected Fees" value="N/A" />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <AccountCard title="Total Free CPU" value="6.12354" />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <AccountCard title="Requested CPU for other total:" value="6.12354" />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <AccountCard title="Request Free CPU" value="N/A" />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <AccountCard title="1 Day Earning" value="6.12354" />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <AccountCard title="Deposited WAX to earn:" value="6.12354" />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <AccountCard title="15 Days Earning" value="6.12354" />
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <AccountCard title="30 Days Earning" value="6.12354" />
            </Grid>
          </Grid>

          <Box sx={{ mt: 3, display: 'flex', justifyContent: 'center', gap: 2 }}>
            <Button variant="contained" color="primary" endIcon={<ArrowForwardIosIcon />}>
              Go to Cpu4Sale
            </Button>
            <Button variant="contained" color="primary" endIcon={<ArrowForwardIosIcon />}>
              Go to LimitlessWAX
            </Button>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}

export default Account;
