import { Box, Typography, Container, styled, Button } from "@mui/material";
import Footer from "../components/Footer";
import Header from "../components/Header";

const StyledButton = styled(Button)({
  width: 120,
  color: "white",
  border: "1px solid white",
  textTransform: "none",
  marginBottom: 120
})

const Error = () => {
  return (
    <Box sx={{ background: 'linear-gradient(90deg, rgba(142,38,63,1) 0%, rgba(37,32,33,1) 100%)' }} color="white">
      <Container maxWidth="xl">
        <Header />
        <Box sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column"
        }}>
          <img width="280px" src="images/cpu-image.png" alt="cpu-image" />
          <Typography variant="h3" gutterBottom sx={{ textAlign: "center", py: 4, fontSize: { xs: 22, sm: 36, md: 48 } }}>
            Please log in to to see this <br /> feature in LimitlessWAX.
          </Typography>
          <StyledButton>
            Login
          </StyledButton>
        </Box>
      </Container>

      <Footer />
    </Box>
  )
}

export default Error;
