import { Container, Typography, Box, CssBaseline, Link } from '@mui/material';
import LoginButtons from '../components/LoginButtons';
import { ThemeProvider } from '@mui/material/styles';
import { createTheme } from '@mui/material/styles';
import { red } from '@mui/material/colors';

const theme = createTheme({
  palette: {
    primary: {
      main: '#556cd6',
    },
    secondary: {
      main: '#19857b',
    },
    error: {
      main: red.A400,
    },
    background: {
      default: '#fff',
    },
  },
  typography: {
    fontFamily: 'Arial, sans-serif',
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          borderRadius: '8px',
          padding: '10px 20px',
          fontWeight: 'bold',
        },
      },
    },
  },
});

const Login = () => {
  const handleAnchorClick = () => {
    alert('Anchor button clicked');
  };

  const handleWaxClick = () => {
    alert('WAX Cloud Wallet button clicked');
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container maxWidth="sm">
        <Box sx={{ my: 4, textAlign: 'center' }}>
          <Typography variant="h4" component="h1" gutterBottom>
            To continue please select an option
          </Typography>
          <LoginButtons onAnchorClick={handleAnchorClick} onWaxClick={handleWaxClick} />
        </Box>
        <Link href="/about-us">Learn more</Link>
      </Container>
    </ThemeProvider>
  );
}

export default Login;
