import React from 'react';
import {
	Box,
	Container,
	Grid,
	Typography,
	useMediaQuery,
	useTheme,

} from '@mui/material';
const Footer: React.FC = () => {
	const theme = useTheme();
	const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

	return (
		<Box
			component="footer"
			sx={{
				py: 5,
				backgroundColor: '#2C1B18', // Set the background color from the image
				color: '#FFFFFF',
			}}
		>
			<Container maxWidth="lg">
				<Grid container spacing={2} justifyContent="space-between" alignItems="flex-start">
					{/* Logo Section */}
					<Grid item xs={12} sm={4}>
						<img src="images/logo.png" alt="Logo" />
						<Typography variant="body2">
							If you need help or have any questions, <br /> please go through our discord.
						</Typography>
						<Typography variant="body2">
							<br />
							Limitlesswax Copyright  2022
						</Typography>
					</Grid>

					{/* Navigation Links */}
					<Grid item xs={12} sm={8}>
						<Grid container spacing={2} justifyContent="space-around">
							<Grid item xs={6} sm={3}>
								<Typography variant="body2"> <br />Home</Typography>
								<Typography variant="body2"> <br />Account</Typography>
							</Grid>
							<Grid item xs={6} sm={3}>
								<Typography variant="body2"> <br />Cpu4sale</Typography>
								<Typography variant="body2"> <br />LimitlessWAX</Typography>
							</Grid>
							<Grid item xs={6} sm={3}>
								<Typography variant="body2"> <br />Github</Typography>
								<Typography variant="body2"> <br />Discord</Typography>
							</Grid>
							<Grid item xs={6} sm={3}>
								<Typography variant="body2"> <br />Privacy</Typography>
								<Typography variant="body2"> <br />Terms</Typography>
							</Grid>
						</Grid>
					</Grid>
				</Grid>
			</Container>
		</Box>
	);
};

export default Footer;
