import { createTheme } from '@mui/material/styles';

const theme = createTheme({
  palette: {
    mode: 'dark', // Assuming a dark mode theme
    primary: {
      main: '#800020', // Example dark red color, adjust as needed
    },
    secondary: {
      main: '#F08080', // Example lighter accent color
    },
    background: {
      default: '#121212', // Dark background
      paper: '#efefef', // Slightly lighter for cards/sections
    }
  },
  typography: {
    fontFamily: 'Roboto, sans-serif', // Or your preferred font
    // fontFamily: 'Arial, sans-serif', // Or your preferred font
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: '24px', // Rounded buttons
          textTransform: 'none', // Prevent uppercase transformation
        },
      },
    },
  },
});

export default theme;
