import React from 'react';
import { Box, Typography, CardContent } from '@mui/material';

interface MemberCardProps {
  name: string;
  description: string;
}

const MemberCard: React.FC<MemberCardProps> = ({ name, description }) => {
  return (
    <Box
      sx={{
        bgcolor: 'transparent',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        color: 'white',
        height: '100%',
        p: { xs: 2, sm: 3 }
      }}
    >
      <Box
        sx={{
          width: { xs: 100, sm: 150, md: 220 },
          height: { xs: 100, sm: 150, md: 220 },
          bgcolor: 'rgb(140,36,63)',
          borderRadius: '50%',
          mb: { xs: 2, sm: 3 },
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          textAlign: 'center',
        }}
      >
        {/* Placeholder for Avatar */}
        <Typography variant="h6"> </Typography>
      </Box>
      <CardContent sx={{ width: '100%' }}>
        <Typography
          variant="h5"
          component="h3"
          gutterBottom
          sx={{
            textAlign: 'center',
            fontSize: { xs: '1.2rem', sm: '1.4rem', md: '1.5rem' }
          }}
        >
          {name}
        </Typography>
        <Typography
          variant="body2"
          sx={{
            textAlign: 'center',
            fontSize: { xs: '0.875rem', sm: '0.9rem', md: '1rem' }
          }}
        >
          {description}
        </Typography>
      </CardContent>
    </Box>
  );
};

export default MemberCard; 