import React, { useState } from 'react';
import {
  styled,
  Toolbar,
  IconButton,
  Typography,
  Button,
  Box,
  useMediaQuery,
  useTheme,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Link,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import InfoIcon from '@mui/icons-material/Info';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import WavesIcon from '@mui/icons-material/Waves';

const StyledButton = styled(Button)({
  borderRadius: 3,
  fontSize: 16,
  marginLeft: 3,
});

const Header: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [drawerOpen, setDrawerOpen] = useState(false);

  const handleDrawerToggle = () => {
    setDrawerOpen(!drawerOpen);
  };

  const navItems = [
    { text: 'About Us', icon: <InfoIcon color="primary" /> },
    { text: 'Account', icon: <AccountCircleIcon color="primary" /> },
    { text: 'Cpu4sale', icon: <ShoppingCartIcon color="primary" /> },
    { text: 'Limitlesswax', icon: <WavesIcon color="primary" /> },
  ];

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
      <Typography variant="h6" sx={{ my: 2 }}>
        ki.fg.wam
      </Typography>
      <List>
        {navItems.map((item) => (
          <ListItem key={item.text} disablePadding>
            <ListItemButton sx={{ textAlign: 'left' }}>
              <ListItemIcon sx={{ fill: 'dodgerblue' }}>{item.icon}</ListItemIcon>
              <ListItemText primary={item.text} sx={{ color: 'black' }} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <Box position="static" sx={{ backgroundColor: 'transparent', boxShadow: 'none', py: 4 }}>
      <Toolbar sx={{ justifyContent: 'space-between' }}>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2, display: { sm: 'none' } }}
            onClick={handleDrawerToggle}
          >
            <MenuIcon />
          </IconButton>
          <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
            <Link href="/">
              <img src="images/logo.png" alt="Logo" />
            </Link>
          </Box>
        </Box>

        {isMobile ? null : (
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <StyledButton onClick={() => window.location.assign('/about')} color="inherit">About Us</StyledButton>
            <StyledButton onClick={() => window.location.assign('/account')} color="inherit">Account</StyledButton>
            <StyledButton onClick={() => window.location.assign('/cpu4sale')} color="inherit">Cpu4sale</StyledButton>
            <StyledButton onClick={() => window.location.assign('/limitlesswax')} color="inherit">Limitlesswax</StyledButton>
            <StyledButton onClick={() => window.location.assign('/login')} color="inherit" variant="outlined">Login</StyledButton>
          </Box>
        )}
      </Toolbar>
      <Drawer
        variant="temporary"
        open={drawerOpen}
        onClose={handleDrawerToggle}
        ModalProps={{
          keepMounted: true, // Better open performance on mobile.
        }}
        sx={{
          display: { xs: 'block', sm: 'none' },
          '& .MuiDrawer-paper': { boxSizing: 'border-box', width: 240 },
        }}
      >
        {drawer}
      </Drawer>
    </Box>
  );
};

export default Header;

