import { Box, Container } from '@mui/material';
import HeroSection from '../components/About/HeroSection';
import MembersSection from '../components/About/MemberSection';
import PartnersSection from '../components/About/ParterSection';
import Community from '../components/Community';
import Header from '../components/Header';
import Footer from '../components/Footer';

const AboutUs = () => {
  return (
    <Box sx={{ background: 'linear-gradient(90deg, rgba(142,38,63,1) 0%, rgba(37,32,33,1) 100%)' }} color="white">
      <Container maxWidth="xl" sx={{ py: 4 }}>
        <Header />
        <HeroSection />
        <Community />
        <MembersSection />
        <PartnersSection />
      </Container>

      <Footer />
    </Box>
  );
};

export default AboutUs;
